// ==========================================================================
// News Layout
// ==========================================================================

.news {
    background-color: $color-news-bg;
    color: $color-tuna;

    .news-image {
        width: 100%;
    }

    .icon {
        float: left;
        padding: 20px;
        padding-left: 0;
    }

    hr {
        margin-top: 30px;
        border: none;
        border-bottom: 1px solid lighten($color-grey, 20%);
    }

    ul {
        list-style-type: none;
        margin: 30px;
        li {
            font-size: 14px;
            &:first-child {
                font-weight: bold;
            }
        }
    }

    .flex-container {
        @media (min-width: $breakpoint-sm) {
            display: flex;
            gap: 30px;
            > div {
                flex: 1 0;
            }
            > div:last-of-type {
                display: none;
            }
        }
        @media (min-width: $breakpoint-lg) {
            > div:last-of-type {
                display: block;
            }
        }
    }

    .news-item {
        border: 1px solid lighten($color-grey, 20%);
        margin-top: 15px;
        margin-bottom: 30px;
        box-shadow:0 0 40px darken(white, 10%);
        position: relative;
    }

    .newstag {
        text-transform: uppercase;
        position: absolute;
        top: 10px;
        right: 0;
        padding: 2px 10px;
        font-size: 12px;
        font-weight: 600;
        color: $color-white;
        &:hover {
            text-decoration: underline;
        }
    }

    .news-footer {
        margin: 30px 10px;
        display: flex;
        justify-content: space-around;
        max-width: 100%;
        gap: 40px;
        img {
            width: 100%;
            filter: grayscale(100%);
            &:hover {
                filter: inherit;
            }
        }
        .news-footer-item {
            position: relative;
            &:hover div {
                display: inherit;
                &.arrow {
                    position: absolute;
                    height: 0;
                    left: calc(50% - 50px);
                    $w: 100px;
                    width: $w; 
                    left: calc(50% - ($w / 2));
                    bottom: calc(100% + 15px);
                    border-left: $w / 2 solid transparent;
                    border-right: $w / 2 solid transparent;
                    border-top: $w / 2 solid;
                }
                &.box {
                    position: absolute;
                    bottom: calc(100% + 65px);
                    $w: 240px;
                    width: $w;
                    left: calc(50% - (#{$w} / 2));
                    background-color: $color-tuna;
                    text-align: center;
                    h3, p, hr {
                        margin: 15px 0;
                        padding: 0 10px;
                        color: $color-white;
                    }
                    h3 {
                        font-size: 18px;
                        font-weight: 400;
                    }
                    hr {
                        border-width: 3px;
                        width: 1%;
                        margin: 0 auto;
                    }
                }
            }
        }
    }

    h1 {
        margin: 0;
        font-size: 43.2px;
        font-weight: 600;
        @media (min-width: $breakpoint-sm) {
            float: left;
        }
    }

    h2 {
        font-size: 26px;
        font-weight: 600;
        text-align: center;
        @media (min-width: $breakpoint-sm) {
            text-align: right;
        }
    }

    h3 {
        font-size: 16px;
        min-height: 48px;
    }

    p {
        font-size: 13px;
        color: darken($color-grey, 20%);
    }

    @each $class, $color in $service-colors {
        .#{$class} {
            .button {
                @include btn($color-white, map-get($service-colors, $class));
            }
            h3 {
                color: map-get($service-colors, $class);
            }
            .newstag {
                background-color: map-get($service-colors, $class);
            }
        }
    }

    a:hover {
        text-decoration: none;
    }

}